<template>
  <div class="list">
    <div class="content">
      <div class="header-sub">
        <div class="title">
          <div class="breadcrumb-item-link">设备日志</div>
          <span class="empty"></span>
        </div>
      </div>
      <div class="filter-search">
        <div class="filter-search-left">
          <div class="filter-select mr-12">
            <StatusSelect placeholder="选择设备名称"
                          :filterable ='true'
                          :type = 2
                          v-model="search.deviceName"></StatusSelect>
          </div>
          <div class="filter-select mr-12">
            <StatusSelect placeholder="选择设备编号"
                          :filterable ='true'
                          :type = 3
                          v-model="search.deviceCode"></StatusSelect>
          </div>
          <div class="filter-btn">
            <Button type="default"
                    @click="loadTableData"
                    :loading="pageInfo.loading">
              <span class="iconfont icon-chazhao"></span>
            </Button>
          </div>
        </div>
      </div>
      <div class="user-table">
        <Table class="m-table no-scroll no-border"
               stripe
               :loading="pageInfo.loading"
               :columns="tableColumns"
               :row-class-name="rowClassName"
               @on-row-click="onRowClick"
               :data="tableData"></Table>
      </div>
      <div class="pagination">
        <Page style=""
              @on-change="onPaginationChange"
              @on-page-size-change="onPaginationSizeChange"
              :current="pageInfo.pageIndex"
              :page-size="pageInfo.pageSize"
              :total="pageInfo.totalElements"
              show-elevator
              show-sizer
              show-total />
      </div>
    </div>
  </div>
</template>

<script>
import StatusSelect from '@/components/device-select'
import LaboratoryList from '@/components/laboratory-list'
import { getLogList } from '@/api/device'
export default {
  components: {
    StatusSelect,
    LaboratoryList
  },
  data () {
    return {
      search: {
        deviceName: '',
        deviceCode: ''
      },
      deviceId1: '',
      deviceId2: '',
      pageInfo: {
        loading: false,
        pageIndex: 1,
        pageSize: 10,
        totalElements: 30
      },
      tableData: [],
      maxlength: 50,
      tableColumns: [
        {
          title: '用户名',
          key: 'userId',
          align: 'left'
        },
        {
          title: '姓名',
          key: 'userName',
          align: 'left'
        },
        {
          title: '时间',
          key: 'operateTime',
          align: 'left'
        },
        {
          title: '设备名称',
          key: 'deviceName',
          align: 'left'
        },
        {
          title: '设备编号',
          key: 'deviceCode',
          align: 'left'
        },
        {
          title: '操作类型',
          key: 'operateType',
          align: 'left'
        },
        {
          title: 'IP',
          key: 'ip',
          align: 'left'
        },
        {
          title: '所属区域',
          key: 'area',
          align: 'left'
        }
      ],
      currentIndex: 0
    }
  },
  mounted () {
    this.loadTableData()
  },
  methods: {
    loadTableData () {
      this.pageInfo.loading = true
      let { pageIndex, pageSize } = this.pageInfo
      let { deviceName, deviceCode } = this.search
      getLogList({pageIndex, pageSize, deviceName, deviceCode}).then(res => {
        this.pageInfo.loading = false
        let {code, data} = res
        if (code === 200) {
          this.tableData = data.list
          this.pageInfo.totalElements = data.total
        }
      })
    },
    getDeviceId () {
      if (this.deviceId1 && this.deviceId2) {
        return this.deviceId1 + ',' + this.deviceId2
      } else if (this.deviceId1 && !this.deviceId2) {
        return this.deviceId1
      } else if (!this.deviceId1 && this.deviceId2) {
        return this.deviceId2
      } else {
        return ''
      }
    },
    onRowClick () {},
    onPaginationChange (index) {
      this.pageInfo.pageIndex = index
      this.loadTableData(false)
    },
    onPaginationSizeChange (size) {
      this.pageInfo.pageSize = size
      this.loadTableData(true)
    },
    rowClassName (row, index) {
      if (row.id == this.currentIndex) {
        return 'demo-table-info-row'
      } else {

      }
    },
    setConfirm () {

    },
    confirm (flag) {
      if (flag) {
        this.setConfirm()
      } else {

      }
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../../../style/varient';
@size-padding: 10px;

.list {
  width: 100%;
  height: 100%;
  .content {
    width: 100%;
    height: 100%;
    box-shadow: 0px -1px 2px 0px rgba(0, 0, 0, 0.05),
      /*上边阴影  红色*/ -1px 0px 2px 0px rgba(0, 0, 0, 0.05),
      /*左边阴影  绿色*/ 1px 0px 2px 0px rgba(0, 0, 0, 0.05),
      /*右边阴影  蓝色*/ 0px 1px 2px 0px rgba(0, 0, 0, 0.05); /*下边阴影  黄色*/
    padding: 24px;
  }
}
</style>



